
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'CikeBaidu',
directives: {
TransferDom
},
components: {
Swiper,
SwiperItem,
Step,
StepItem,
Checker,
CheckerItem,
XInput,
Group,
Popup,
XDialog,
XButton
},
data () {
return {
swiperItemIndex: 0,
willClassList: [
{id: '1', willItem: '成人口语'},
{id: '2', willItem: '零基础突破'},
{id: '3', willItem: '旅游英语'},
{id: '4', willItem: '职场英语'},
{id: '5', willItem: '商务英语'},
{id: '6', willItem: '移民英语'},
{id: '7', willItem: '自然拼读'},
{id: '8', willItem: '音标纠音'},
{id: '9', willItem: '英语语法'},
{id: '10', willItem: '综合英语进阶'}
],
willClassItem: [],
targetList: [
{id: '1', willItem: '工作需要'},
{id: '2', willItem: '出国旅行'},
{id: '3', willItem: '专业考试'},
{id: '4', willItem: '移民'},
{id: '5', willItem: '教育子女'},
{id: '6', willItem: '自我提升'},
{id: '7', willItem: '兴趣爱好'},
{id: '8', willItem: '其他'}
],
targetItem: null,
levelList: [
{id: '1', willItem: '零基础'},
{id: '2', willItem: '薄弱'},
{id: '3', willItem: '一般'},
{id: '4', willItem: '良好'},
{id: '5', willItem: '熟练'},
{id: '6', willItem: '精通'}
],
levelItem: null,
dayList: [
{id: '1', willItem: '一天'},
{id: '2', willItem: '两天'},
{id: '3', willItem: '三天'},
{id: '4', willItem: '四天'},
{id: '5', willItem: '五天'},
{id: '6', willItem: '六天'},
{id: '7', willItem: '七天'}
],
dayItem: null,
hoursList: [
{id: '1', willItem: '1小时'},
{id: '2', willItem: '2小时'},
{id: '3', willItem: '3小时'},
{id: '4', willItem: '4小时'},
{id: '5', willItem: '5小时'},
{id: '6', willItem: '6小时'},
{id: '7', willItem: '7小时'},
{id: '8', willItem: '8小时'}
],
hoursItem: null,
isShowVideo: false,
isShowYuyue: false,
isShowOffer: false,
isShowOfferTwo: false,
isShowDialog: false,
isTopTips: false,
topTipsText: '',
isClassList: false
}
},
watch: {
isTopTips (val) {
if (val) {
setTimeout(() => {
this.isTopTips = false
}, 2000)
}
}
},
filters: {},
mounted () {

},
computed: {},
methods: {
next () {
switch (this.swiperItemIndex) {
case 0:
if (this.willClassItem.length === 0) {
this.topTipsText = '您还没有选择意向课程'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 1:
console.log(this.targetItem)
if (this.targetItem === null) {
this.topTipsText = '您还没有选择学习目的'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 2:
console.log(this.levelItem)
if (this.levelItem === null) {
this.topTipsText = '您还没有选择英语水平'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 3:
console.log(this.dayItem, this.hoursItem)
if (this.dayItem === null || this.hoursItem === null) {
this.topTipsText = '您还没有选择每天学习时间'
this.isTopTips = true
} else { this.isShowOffer = true }
break
}
},
checkPlan () {
this.isShowOffer = false
this.isShowOfferTwo = true
}
}
}



